:root {
  --start-gradient-color: #730505;
  --end-gradient-color: #0c0b0b;
}

.error {
  background-image: url("../iron-man.777e803d.png"), radial-gradient( 75.97% 258.93% at 61.53% 47.37%, var(--start-gradient-color) 0%, var(--end-gradient-color) 100% );
  background-position: 100% 100%;
  background-repeat: no-repeat;
  padding-top: 278px;
  padding-bottom: 189px;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .box {
    background-image: url("../iron-man-desk@2x.cae1e0cd.png"), radial-gradient( 75.97% 258.93% at 61.53% 47.37%, var(--start-gradient-color) 0%, var(--end-gradient-color) 100% );
  }
}

.error-container {
  width: 1440px;
  padding-left: 32px;
  padding-right: 32px;
}

.error-text {
  text-transform: uppercase;
  color: rgba(255, 255, 255, .7);
  margin: 0 0 16px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
}

.error-title {
  letter-spacing: -.02em;
  color: #fafafa;
  margin: 0 0 16px;
  font-size: 150px;
  font-weight: 500;
  line-height: 1;
}

.error-message {
  letter-spacing: .02em;
  color: #fafafa;
  margin: 0 0 40px;
  font-size: 16px;
  line-height: 1.25;
}

.back-home {
  width: 85px;
  text-transform: uppercase;
  color: #fafafa;
  border: 1px solid rgba(250, 250, 250, .5);
  border-radius: 1000px;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  text-decoration: none;
  display: block;
}

